@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&family=Roboto:wght@400;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Comic+Neue:wght@300&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.header {
  background-color: #fff;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 1px 1px 5px #d6d6d8;
}

.logo > img {
  width: 85px;
}

.nav-bar > ul {
  list-style: none;
  display: flex;
}

.nav-bar > ul > li {
  padding: 0 20px;
  font-weight: 700;
  display: flex;
}

.nav-bar > ul > li > a {
  color: #292828;
  padding: 0 5px;
  font-size: 18px;
  text-decoration: none;
}

.nav-bar > ul > li > a:hover {
  color: #fead17;
}

.small-btn {
  min-width: 4.5rem;
  min-height: 2rem;
  color: #fff;
  text-align: center;
  background-color: #909090;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: 500;
}

.search-bar {
  align-items: center;
  display: flex;
}

.search-bar:hover {
  color: #fead17;
}

.input-box > input {
  color: #000;
  border: none;
  outline: none;
  padding: 0 10px;
  font-size: 1.2rem;
  font-weight: bolder;
}

.restaurant-list {
  max-width: 1260px;
  flex-flow: wrap;
  justify-content: space-between;
  gap: 1rem;
  margin-inline: auto;
  display: flex;
}

.restaurant-card {
  width: 300px;
  min-height: 320px;
  color: #282c3f;
  letter-spacing: 0;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  font-family: Comic Neue, cursive;
  line-height: 1.2;
  transition-property: all;
  display: flex;
}

.restaurant-card:hover {
  border: 1px solid #9c9a9a;
  box-shadow: 1px 1px 5px #d6d6d8;
}

.restaurant-food-img > img {
  width: 254px;
  height: 160px;
}

.restaurant-food-img {
  padding-top: 20px;
}

.shimmer {
  width: 400px;
  height: 300px;
  background-color: gray;
}

.rating-box {
  width: 3rem;
  height: 1.5rem;
  background-color: #50d686;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.cuisines {
  width: 100%;
  text-align: center;
  flex-wrap: wrap;
  gap: 10px;
  line-height: 1.2;
  display: flex;
}

.restaurant-name {
  word-break: break-word;
  font-family: ProximaNova, arial, Helvetica Neue, sans-serif;
  font-size: 17px;
  font-weight: 600;
  line-height: 1.2;
}

.restaurant-heading {
  justify-content: flex-start;
  display: flex;
}

.cuisines-detail {
  color: #686b78;
  word-break: break-word;
  margin-top: 4px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.2;
}

.details-box {
  width: 100%;
  justify-content: space-around;
  font-size: 14px;
  display: flex;
}

@media screen and (max-width: 640px) {
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  .header {
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: flex;
  }

  .input-box > input {
    width: 100px;
  }

  .logo {
    min-height: 50px;
  }

  .search-bar {
    display: flex;
  }

  .nav-bar > ul {
    flex-flow: column wrap;
    display: flex;
  }

  .nav-bar > ul > li {
    gap: 5px;
    padding-bottom: 4px;
  }
}

